/***
Colors, will be in variable.scss
*/

@import "variables.scss";

h1 {
    font-size: 2rem;
    color: $green;
}


section {

    border: 2px solid black;
    max-width: 100vw;
    overflow: hidden;
}

img {
    max-width: 100%;
    object-fit: cover;
    object-position: bottom;
}